import React from 'react'
import PropTypes from 'prop-types'
import BlcLogoBase from 'components/BlcLogo/BlcLogoBase';
import BlcFloatImage from 'components/BlcLogo/Float/BlcFloatImage';
import { color } from 'public/styles';

const blcLogoFloat = props => (
	<BlcLogoBase margin={props.margin}>
		<BlcFloatImage size={props.size} color={props.color} />
	</BlcLogoBase>
);

blcLogoFloat.propTypes = {
	color: PropTypes.oneOf([color.asphalt, color.backlot, color.white]),
	margin: PropTypes.string,
	size: PropTypes.number
}

blcLogoFloat.defaultProps = {
	color: color.white,
	margin: '0',
	size: 48
}

export default blcLogoFloat;
