import React from 'react';
import PropTypes from 'prop-types';
import BlcLogoBase from 'components/BlcLogo/BlcLogoBase';
import BlcTransportImage from 'components/BlcLogo/Transport/BlcTransportImage';
import { color } from 'public/styles';

const blcLogoTransport = props => (
	<BlcLogoBase margin={props.margin}>
		<BlcTransportImage size={props.size} color={props.color} />
	</BlcLogoBase>
);

blcLogoTransport.propTypes = {
	color: PropTypes.oneOf([color.asphalt, color.backlot, color.white]),
	margin: PropTypes.string,
	size: PropTypes.number,
}

blcLogoTransport.defaultProps = {
	color: color.white,
	margin: '0',
	size: 96
}


export default blcLogoTransport;
